import VueRouter from 'vue-router'
import AddLogs from "@/pags/AddLogs.vue";
import HomePage from "@/pags/HomePage.vue";
import Information from "@/pags/Information.vue";
import MyLogs from "@/pags/MyLogs.vue";
import LoginPage from "@/pags/LoginPage.vue";
import AccountLogin from "@/components/login/AccountLogin.vue";
import EmailLogin from "@/components/login/EmailLogin.vue";
import DefaultIndex from "@/pags/DefaultIndex.vue";
import RegisterPage from "@/pags/RegisterPage.vue";
import AccountRegister from "@/components/register/AccountRegister.vue";
import EmailRegister from "@/components/register/EmailRegister.vue";

const router =  new VueRouter({
    routes: [
        {
          path: '/',
          // redirect: '/HomePage/Information'
          redirect: '/DefaultIndex/HomePage/information'
          //   redirect: '/RegisterPage/AccountRegister'
        },{
            path: '/LoginPage',
            redirect: '/LoginPage/AccountLogin'
        },{
            path: '/RegisterPage',
            redirect: '/RegisterPage/AccountRegister'
        },{
            name: 'LoginPage',
            path: '/LoginPage',
            component: LoginPage,
            children: [{
                name: 'AccountLogin',
                path: 'AccountLogin',
                component: AccountLogin
            },{
                name: 'EmailLogin',
                path: 'EmailLogin',
                component: EmailLogin
            }]
        },{
            name: 'RegisterPage',
            path: '/RegisterPage',
            component: RegisterPage,
            children: [{
                name: 'AccountRegister',
                path: 'AccountRegister',
                component: AccountRegister
            },{
                name: 'EmailRegister',
                path: 'EmailRegister',
                component: EmailRegister
            }]
        },{
            name: 'DefaultIndex',
            path: '/DefaultIndex',
            component: DefaultIndex,
            children: [{
                name: 'HomePage',
                path: 'HomePage',
                components: {
                    defaultIndex: HomePage
                },
                children: [{
                    name: 'Information',
                    path: 'Information',
                    components: {
                        information:Information
                    }
                },{
                    name: 'MyLogs',
                    path: 'MyLogs',
                    components: {
                        information: MyLogs
                    },
                },{
                    name: 'AddLogs',
                    path: 'AddLogs',
                    components: {
                        information: AddLogs
                    }
                }]
            }]
        }
    ]
})

// eslint-disable-next-line no-unused-vars
router.beforeEach((to, from, next) => {
    if (to.path === '/DefaultIndex/HomePage/MyLogs') {
        if (router.app.$store.state.defaultContext.uid !== '') {
            next()
        } else {
            const item = sessionStorage.getItem("userInformation");
            if (item !== '' && item !== null && item !== undefined) {
                router.app.$store.commit('defaultContext/LOGIN',JSON.parse(item))
                next()
            } else {
                alert("你还没有登录,前往登录")
                router.push({path: '/LoginPage'})
            }
        }
    } else {
        next()
    }
})
// eslint-disable-next-line no-unused-vars
router.afterEach((to, from) => {

})
export default router