<template>
  <div class="nvd">
    <h1>Register to Luoan Website</h1>
    <div class="in-main">
      <div class="in-title">
        <ul>
          <li><router-link to="/RegisterPage/AccountRegister"><span style="margin-left: 30px;margin-right: 60px">账号注册</span></router-link></li>
          <li><router-link to="/RegisterPage/EmailRegister"><span style="margin-left: 60px;margin-right: 30px">邮箱注册</span></router-link></li>
        </ul>
      </div>
      <router-view></router-view>
      <div class="in-information">
        <router-link to="/LoginPage"><span style="margin-right: 80px">已有账号?登录</span></router-link>
        <router-link to=""><span style="margin-left: 80px">忘记密码</span></router-link>
      </div>
      <div class="in-btn">
        <button @click="register">注册</button>
      </div>
    </div>
  </div>
</template>

<script>

import axios from "axios";
import {mapGetters} from "vuex";

export default {
  name: "RegisterPage",
  data() {
    return {
      verificationRegister: false
    }
  },
  computed: {
    ...mapGetters('register',['getUsername','getAccount','getPassword','getCode','getType']),
  },
  methods: {
    register() {
      if (this.verificationRegister) {
        const data = {
          // operation: 'registerPage',
          username: this.getUsername,
          account: this.getAccount,
          password: this.getPassword,
          registerCode: this.getCode,
        }
        axios.post(this.$store.state.host + `register/${this.getType}`, data).then(
            require => {
              let title = '注册失败'
              let message = 'error'
              if (require.data.data.registerStatus) {
                title = '注册成功'
                message = '请妥善保管好账号密码'
              }
              this.$alert(message, title, {
                confirmButtonText: '确定',
                callback: action => {
                  this.$message({
                    type: 'info',
                    message: `action: ${action}`
                  });
                }
              })
              if (require.data.data.registerStatus)
                this.$router.push('/LoginPage')
            }
        )
      } else {
        alert("输入不合法")
      }
    }
  },
  mounted() {
    this.$bus.$on("verificationRegister",v => this.verificationRegister = v)
  }
}
</script>

<style scoped>
  a, li, ul {
    padding: 0;
    list-style: none;
    text-decoration: none;
  }
  .nvd {
    display: block;
    /*height: 575px;*/
    width: 100%;
    margin: 100px auto;
    text-align: center;
  }
  .in-main {
    width: 500px;
    margin: 30px auto;
    padding: 10px;
    border-radius: 50px;
    background-color: aliceblue;
  }
  .in-title {

  }
  .in-main .in-title ul {
    width: 350px;
    margin: auto;
    display: flex;
    padding-bottom: 3px;
    border-bottom: 1px solid rosybrown;
  }
  .in-main .in-title ul li span {
    font-size: large;
    color: #0c0c0c;
  }

  .in-main .in-btn {
    margin-top: 20px;
    margin-bottom: 30px;
  }
  .in-main .in-btn button {
    width: 250px;
    height: 40px;
    margin-bottom: 10px;
    border: none;
    border-radius: 50px;
  }
  .in-btn:hover button {
    background-color: #fcf4c0;
  }
  .nvd .in-information {
    margin-bottom: 40px;
  }
  .nvd .in-information span {
    font-size: 10px;
  }
</style>