</template>

<script>
import axios from "axios";
import {mapMutations} from "vuex";

export default {
  name: "EmailRegister",
  data() {
    return {
      username: '',
      account: '',
      email: '@qq.com',
      firstPassword: '',
      lastPassword: '',
      code: '',
      confirmTxt: '',
      isConfirmTxt: false,
      timer: '',
      user: false,
      acc: false,
      pass: false,
      pwd: false,
      user1: false,
      acc1: false,
      pass1: false,
      pwd1: false,
    }
  },
  computed: {
    verificationRegister() {
      return !this.pwd && this.pwd1 &&
          !this.pass && this.pass1 &&
          !this.acc && this.acc1 &&
          !this.user && this.user1 && this.code !== ''
    }
  },
  watch: {
    verificationRegister(newValue) {
      this.$bus.$emit("verificationRegister",newValue)
    }
  },
  methods: {
    ...mapMutations('register',['SET_USERNAME','SET_ACCOUNT','SET_PASSWORD','SET_CODE','SET_TYPE']),
    duplicateQuery() {
      const account = this.account + this.email
      axios.get(this.$store.state.host + `duplicate/email/${account}`).then(
          require => {
            if (require.data.data.duplicateOrNot) {
              this.acc = true
            } else {
              this.acc = false
              this.acc1 = true
              this.SET_ACCOUNT(this.account  + this.email)
            }
          }
      )
    },
    verificationUsername() {
      if (/.{2,10}/.test(this.username)) {
        this.user = false
        this.user1 = true
        this.SET_USERNAME(this.username)
      } else {
        this.user = true
      }
    },
    verificationAccount() {
      if (/^[0-9]{6,10}$/.test(this.account)) {
        this.duplicateQuery()
      } else {
        this.acc = true
      }
    },
    verificationFirstPassword() {
      if (/^\w{6,16}$/.test(this.firstPassword)) {
        this.pass = false
        this.pass1 = true
      } else {
        this.pass = true
      }
    },
    verificationLastPassword() {
      if (this.firstPassword !== '' && this.lastPassword === this.firstPassword) {
        this.pwd = false
        this.pwd1 = true
        this.SET_PASSWORD(this.lastPassword)
      } else {
        this.pwd = true
      }
    },
    verificationCode() {
      this.SET_CODE(this.code)
    },
    getRegisterEmailCode() {
      if (!this.acc && this.acc1) {
        this.confirmTxt = 60
        this.isConfirmTxt = true
        this.timer = setInterval(() => {
          if (this.confirmTxt === 0) {
            this.confirmTxt = ''
            this.isConfirmTxt = false
            clearInterval(this.timer)
          } else {
            this.confirmTxt--
          }
        },1000)
        const account = this.account + this.email
        axios.get(this.$store.state.host + `register/${account}`).then(
            require => {
              if (!require.data.data.registerStatus) alert("邮箱已被注册")
            }
        )
      } else {
        alert("邮箱错误")
      }
    }
  },
  mounted() {
    this.SET_TYPE("emailRegister")
  },
  beforeDestroy() {
    this.$bus.$emit("verificationRegister",false)
  }
}
</script>

