<template>
  <div id="nvd">
    <div class="middle">
      <LeftList class="middle-left"/>
      <div class="middle-content">
        <Dynamics class="topic" v-for="topic in topicInformation" :key="topic.id" :topic="topic"></Dynamics>
      </div>
      <RightList class="middle-right"/>
    </div>
  </div>
</template>

<script>
import LeftList from "@/components/home/middle/LeftList.vue";
import RightList from "@/components/home/middle/RightList.vue";
import Dynamics from "@/components/home/middle/Dynamics.vue";
import axios from "axios";

export default {
  name: "Information",
  components: {LeftList,RightList,Dynamics},
  data() {
    return {
      topicInformation: []
    }
  },
  methods: {
    refreshHomePage() {
      axios({url: this.$store.state.host + 'main', method: "get"}).then(
          require => {
            this.topicInformation = require.data.data.topicInformations
          },
          error => {
            console.log(error.message)
          }
      )
    }
  },
  beforeMount() {
    this.refreshHomePage()
    this.$bus.$on('refreshHomePage',this.refreshHomePage)
  }
}
</script>

<style scoped>
.middle .topic {
  background-color: oldlace;
  margin-bottom: 20px;
  padding: 40px;
}
.middle .middle-left {
  display: inline-block;
  margin: 0;
}
.middle .middle-content {
  display: inline-block;
  /*width: 500px;*/
}
.middle .middle-right {
  display: inline-block;
}
.middle {
  display: flex;
  flex-wrap: wrap;
  width: 1200px;
  margin: 0 auto;
}
</style>