<template>
  <div class="in-main">
    <div class="in-context">
      <div class="in-context-information">
        <span>account</span>
      </div>
      <input class="in-put" type="text" v-model="account" @blur="verificationAccount" placeholder="账号/邮箱/手机号">
    </div>
    <div class="in-context">
      <div class="in-context-information">
        <span>password</span>
      </div>
      <input class="in-put" type="password" v-model="password" @blur="verificationPassword" placeholder="********">
    </div>
  </div>
</template>

<script>
export default {
  name: "AccountLogin",
  data() {
    return {
      account: '',
      password: ''
    }
  },
 methods: {
   verificationAccount() {
     this.$bus.$emit('loginAccount', this.account)
   },
   verificationPassword() {
     this.$bus.$emit('loginPassword', this.password)
   }
 },
  mounted() {
    this.$bus.$emit('loginType', 'accountLogin')
  }
}
</script>

<style scoped>
  .in-main .in-context {
    margin-top: 20px;
  }
  .in-main .in-context span {
    display: block;
    margin-bottom: 10px;
    margin-right: 150px;
    color: slateblue;
    font-size: larger;
  }
  .in-main .in-context .in-put {
    width: 250px;
    height: 30px;
    border-radius:5px;
    border: 1px solid burlywood;
  }

</style>