export default  {
    // name: 'defaultContext',
    namespaced:true,
    actions: {},
    mutations: {
        LOGIN(state,value) {
            state.id = value.id
            state.uid = value.uid
            state.age = value.age
            state.headPortrait_url = value.headportrait_url
            state.birthday = value.birthday
            state.cell_phonenumber = value.cell_phonenumber
            state.fabulous = value.fabulous
            state.email = value.email
            state.gender = value.gender
            state.userName = value.username
            state.registration_time = value.registration_time
            state.userState = true
        },
        EXIT(state) {
            state.userName = ''
            state.uid = ''
            state.headPortrait_url = ''
            state.id = ''
            state.registration_time = ''
            state.age = ''
            state.gender = ''
            state.birthday = ''
            state.email = ''
            state.cell_phonenumber = ''
            state.fabulous = ''
            state.userState = false
        }
    },
    state: {
        userName: '',
        uid: '',
        headPortrait_url: '',
        id: '',
        registration_time: '',
        age: '',
        gender: '',
        birthday: '',
        email: '',
        cell_phonenumber: '',
        fabulous: '',

        userState: false

    },
    getters: {}
}