<template>
  <div id="nvd">
    <div class="log-box">
      <div>
        <span class="head-btn">我的动态</span>
        <hr>
        <div class="title">
          <input type="text" v-model="title" placeholder="请在这里输入标题">
        </div>
        <div class="in-content">
          <el-input
              class="content"
              type="textarea"
              :autosize="{ minRows: 20}"
              placeholder="请输入内容"
              v-model="content">
          </el-input>
        </div>
        <div class="upload">
          <el-upload
              ref="pictureUpload"
              action="#"
              list-type="picture-card"
              :auto-upload="false">
            <i slot="default" class="el-icon-plus"></i>
            <div slot="file" slot-scope="{file}">
              <img
                  class="el-upload-list__item-thumbnail"
                  :src="file.url" alt=""
              >
              <span class="el-upload-list__item-actions">
        <span
            class="el-upload-list__item-preview"
            @click="handlePictureCardPreview(file)"
        >
          <i class="el-icon-zoom-in"></i>
        </span>
        <span
            v-if="!disabled"
            class="el-upload-list__item-delete"
            @click="handleDownload(file)"
        >
          <i class="el-icon-download"></i>
        </span>
        <span
            v-if="!disabled"
            class="el-upload-list__item-delete"
            @click="handleRemove(file)"
        >
          <i class="el-icon-delete"></i>
        </span>
      </span>
            </div>
          </el-upload>
          <el-dialog :visible.sync="dialogVisible">
            <img width="100%" :src="dialogImageUrl" alt="">
          </el-dialog>
        </div>
        <div class="btn">
            <el-button @click="myDynamic" type="info" round>我的动态</el-button>
            <el-button @click="addDynamic" type="info" round>发表动态</el-button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import Qs from 'qs';

export default {
  name: "AddLogs",
  data() {
    return {
      title: '',
      content: '',
      dialogImageUrl: '',
      dialogVisible: false,
      disabled: false,
      file: ''
    }
  },
  methods: {
    myDynamic() {
      this.$router.push({path: '/DefaultIndex/HomePage/MyLogs'})
    },
    addDynamic() {
      let formData = new FormData();
      formData.set("uid",this.$store.state.defaultContext.uid)
      formData.set("title",this.title)
      formData.set("content",this.content)
      formData.set("author",this.$store.state.defaultContext.userName)
      formData.set("headportrait_url",this.$store.state.defaultContext.headPortrait_url)
      const files = this.$refs.pictureUpload.uploadFiles
      for (const file of files) {
        formData.append('files',file.raw)
      }
      axios.post(this.$store.state.host + 'addDynamic',
          formData,
          {'Content-type' : 'multipart/form-data;charset=UTF-8'
          })
      this.picture_utl()
      this.title = ''
      this.content = ''
      this.$refs.pictureUpload.uploadFiles = []
      this.$bus.$emit('refreshMyLogsPage')
      this.myDynamic()
    },
    picture_utl() {

    },
    handleRemove(file) {
      // console.log(file);
      // debugger
      console.log(this.$refs.pictureUpload.uploadFiles)
      let uploadFiles = this.$refs.pictureUpload.uploadFiles
      for (var i = 0; i < uploadFiles.length; i++) {
        if (uploadFiles[i]['url'] === file.url) {
          uploadFiles.splice(i, 1)
        }
      }
    },
    handlePictureCardPreview(file) {
      this.dialogImageUrl = file.url;
      this.dialogVisible = true;
      // axios.defaults.transformRequest = [function (data, config) {
      //   if (!config['Content-Type']) return Qs.stringify(data)
      //   switch (config['Content-Type'].toLowerCase()) {
      //     case 'application/json;charset=utf-8' : {
      //       return JSON.stringify(data)
      //     }
      //     case 'multipart/form-data;charset=UTF-8' : {
      //       return data
      //     }
      //     default: {
      //       return Qs.stringify(data)
      //     }
      //   }
      // }]
    },
    handleDownload(file) {
      // console.log(file);
      console.log(file.url,this.$refs.pictureUpload.uploadFiles)
      window.open(file.url)
    }
  }
}
</script>

<style scoped>
  #nvd {}
  .log-box {
    display: block;
    width: 800px;
    border: 1px solid black;
    margin: auto;
  }
  .head-btn {
    display: block;
    padding: 10px;
    margin-left: 10px;
  }
  .title {
    width: 800px;
    height: 200px;
    background-color: #fff;
    text-align: center;

  }
  .title input {
    width: 400px;
    height: 45px;
    border: none;
    margin-top: 70px;
    font-size: xx-large;
    text-align: center;
  }
  .in-content {
    width: 800px;
    border: none;
    border-radius: 0;
    margin-bottom: 10px;
  }
  .content:focus {
    outline: none !important;
  }
  .in-content .content {
  }
  .upload {
    margin-left: 10px;
    margin-bottom: 10px;
  }
  .btn {
    text-align: right;
    margin: 5px 5px 5px;
  }
</style>