export default {
    // name: 'defaultContext',
    namespaced: true,
    actions: {},
    mutations: {
        SET_USERNAME(state,value) {
            state.username = value
        },
        SET_ACCOUNT(state,value) {
            state.account = value
        },
        SET_PASSWORD(state,value) {
            state.password = value
        },
        SET_CODE(state,value) {
            state.code = value
        },
        SET_TYPE(state,value) {
            state.type = value
        }
    },
    state: {
        username: '',
        account: '',
        password: '',
        code: '',
        type: ''
    },
    getters: {
        getUsername(state) {
            return state.username
        },
        getAccount(state) {
            return state.account
        },
        getPassword(state) {
            return state.password
        },
        getCode(state) {
            return state.code
        },
        getType(state) {
            return state.type
        },
    }
}