<template>
  <div class="nvd">
    <ImgExhibition/>
    <NavigationBar/>
    <keep-alive><router-view name="information"></router-view></keep-alive>
    <div class="footer"></div>
  </div>
</template>

<script>
import ImgExhibition from "@/components/home/ImgExhibition.vue";
import NavigationBar from "@/components/home/NavigationBar.vue";

export default {
  name: "HomePage",
  components: {ImgExhibition,NavigationBar},
  beforeMount() {
    const item = sessionStorage.getItem("userInformation");
    if (item !== '' && item !== null && item !== undefined) {
      this.$store.commit('defaultContext/LOGIN',JSON.parse(item))
    }
  }
}
</script>

<style scoped>
  .nvd {
    border-color: black;
  }
  ImgExhibition {
    display: block;
  }
  .nvd .footer {
    display: block;
    height: 30px;
  }
</style>