<template>
  <div id="nvd">
    <div class="log-box">
      <span class="head-btn">我的动态</span>
      <hr>
      <div class="btn">
        <el-button @click="writeDynamic" type="info" round>写动态</el-button>
      </div>
      <div v-show="topicInformation.length === 0" class="no-logs">
        <span>还没有动态，快去<router-link to="/DefaultIndex/HomePage/AddLogs">写动态</router-link>吧</span>
      </div>
      <div>
        <Dynamics class="logs" v-for="topic in topicInformation" :key="topic.id" :topic="topic" @refresh="refresh" :mySwitch="true"></Dynamics>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import Dynamics from "@/components/home/middle/Dynamics.vue";
export default {
  name: "MyLogs",
  components: {Dynamics},
  data() {
    return {
      topicInformation: []
    }
  },
  methods: {
    refresh() {

      // operation: 'myDynamics',
      // author: this.$store.state.defaultContext.userName

      axios.get(this.$store.state.host + `myDynamics/${this.$store.state.defaultContext.userName}`).then(
          require => {
            // console.log(require.data)
            this.topicInformation = require.data.data.topicInformation
          }
      )
      this.$bus.$emit('refreshHomePage')
    },
    writeDynamic() {
      this.$router.push({path: '/DefaultIndex/HomePage/AddLogs'})
    }
  },
  beforeMount() {
    this.refresh()
    this.$bus.$on('refreshMyLogsPage',this.refresh)
  }
}
</script>

<style scoped>
  #nvd {}
  .log-box {
    display: block;
    width: 800px;
    border: 1px solid black;
    margin: auto;
  }
  .head-btn {
    display: block;
    padding: 10px;
    margin-left: 10px;
  }

  .btn {
    margin: 5px 5px 5px;
  }
  .no-logs {
    display: block;
    height: 500px;
    text-align: -webkit-center;
  }
  .logs {
    padding: 30px;
    margin-top: 15px;
    margin-bottom: 30px;
    background-color: white;
    border-radius: 60px
  }
</style>