<template>
  <div id="nvd">
    <h1>Log in to Luoan Website</h1>
    <div class="in-main">
      <div class="in-title">
        <ul>
          <li><router-link to="/LoginPage/AccountLogin"><span style="margin-left: 30px;margin-right: 60px">账号登录</span></router-link></li>
          <li><router-link to="/LoginPage/EmailLogin"><span style="margin-left: 60px;margin-right: 30px">验证码登录</span></router-link></li>
        </ul>
      </div>
      <router-view></router-view>
      <div class="in-information">
        <router-link to="/RegisterPage"><span style="margin-right: 0px">还没有账号?立即注册</span></router-link>
        <router-link to=""><span style="margin-left: 80px">忘记密码</span></router-link>
      </div>
      <div class="in-btn">
        <button @click="login">登录</button>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import {mapMutations} from "vuex";

export default {
  name: "LoginPage",
  data() {
    return {
      operation: 'loginPage',
      account: '',
      password: '',
      type: ''
    }
  },
  methods: {
    ...mapMutations('defaultContext',{LOGIN: 'LOGIN'}),
    login() {
      const data = {
        // operation: this.operation,
        account: this.account,
        password: this.password,
      }
      axios.post(this.$store.state.host + `sign/${this.type}`, data).then(
        require => {
          const msg = require.data.data
          // console.log(require.data,this.$store.state.host + `sign/${this.type}`,require.data.data.loginStatus)
          if (!msg.loginStatus) {
            this.$alert(msg.message, '登录失败', {
              confirmButtonText: '确定',
              callback: action => {
                this.$message({
                  type: 'info',
                  message: `action: ${ action }`
                });
              }
            })
          } else {
            // console.log(this.$store)
            // this.$store.dispatch('defaultContext/LOGIN',require.data.userInformation)
            const userInformation = require.data.data.userInformation;
            this.LOGIN(userInformation)
            this.saveSession(userInformation)
            this.$router.push({path:'/DefaultIndex/HomePage/information'})
          }
        }
      )
    },
    saveSession(userInformation) {
      sessionStorage.setItem("userInformation",JSON.stringify(userInformation))
    }
  },
  beforeMount() {
    this.$bus.$on('loginAccount',value => this.account = value)
    this.$bus.$on('loginPassword', value => this.password = value)
    this.$bus.$on('loginType', value => this.type = value)
  }
}
</script>

<style scoped>
  a, li, ul {
    padding: 0;
    list-style: none;
    text-decoration: none;
  }
  #nvd {
    display: block;
    height: 575px;
    width: 100%;
    margin: 100px auto;
    text-align: center;
  }
  .in-main {
    width: 500px;
    margin: 30px auto;
    padding: 10px;
    border-radius: 50px;
    background-color: aliceblue;
  }
  .in-title {

  }
  .in-main .in-title ul {
    width: 350px;
    margin: auto;
    display: flex;
    padding-bottom: 3px;
    border-bottom: 1px solid rosybrown;
  }
  .in-main .in-title ul li span {
    font-size: large;
    color: #0c0c0c;
  }

  .in-main .in-btn {
    margin-top: 20px;
  }
  .in-main .in-btn button {
    width: 150px;
    height: 30px;
    margin-bottom: 10px;
    border: none;
    border-radius: 50px;
  }
  .in-btn:hover button {
    background-color: #fcf4c0;
  }

  .in-main .in-information {
    position: relative;
    top: -20px;
    margin-left: 20px;
  }
  .in-main .in-information span {
    font-size: 10px;
  }

</style>