import Vue from "vue";
import Vuex from 'vuex'
import defaultContext from "@/store/defaultContext";
import register from "@/store/register";

Vue.use(Vuex)

export default new Vuex.Store({
    modules: {
        defaultContext,
        register
    },
    state: {
        host: location.protocol + '//' + location.host + '/proxy/'
            // 'http://love.luoande.top:8888/proxy/'
            // 'http://localhost:8081'
            // 'http://smoking.luoande.top'
    }
})