<script>
import axios from "axios";
import {mapMutations} from "vuex";

export default {
  name: "AccountRegister",
  data() {
    return {
      username: '',
      account: '',
      firstPassword: '',
      lastPassword: '',
      user: false,
      acc: false,
      pass: false,
      pwd: false,
      user1: false,
      acc1: false,
      pass1: false,
      pwd1: false,
    }
  },
  computed: {
    verificationRegister() {
      return !this.pwd && this.pwd1 &&
      !this.pass && this.pass1 &&
      !this.acc && this.acc1 &&
      !this.user && this.user1}
  },
  watch: {
    verificationRegister(newValue) {
      this.$bus.$emit("verificationRegister",newValue)
    }
  },
  methods: {
    ...mapMutations('register',['SET_USERNAME','SET_ACCOUNT','SET_PASSWORD','SET_CODE','SET_TYPE']),
    duplicateQuery() {
      // const data = {
        // operation: 'duplicateQuery',
        // type: 'account',
      // }
      axios.get(this.$store.state.host + `duplicate/account/${this.account}`).then(
          require => {
            if (require.data.data.duplicateOrNot) {
              this.acc = true
            } else {
              this.acc = false
              this.acc1 = true
              this.SET_ACCOUNT(this.account)
            }
          }
      )
    },
    verificationUsername() {
      if (/.{2,10}/.test(this.username)) {
        this.user = false
        this.user1 = true
        this.SET_USERNAME(this.username)
      } else {
        this.user = true
      }
    },
    verificationAccount() {
      if (/^[0-9]{6,10}$/.test(this.account)) {
        this.duplicateQuery()
      } else {
        this.acc = true
      }
    },
    verificationFirstPassword() {
      if (/^\w{6,16}$/.test(this.firstPassword)) {
        this.pass = false
        this.pass1 = true
      } else {
        this.pass = true

      }
    },
    verificationLastPassword() {
      if (this.firstPassword !== '' && this.lastPassword === this.firstPassword) {
        this.pwd = false
        this.pwd1 = true
        this.SET_PASSWORD(this.lastPassword)
      } else {
        this.pwd = true
      }
    },
  },
  mounted() {
    this.SET_TYPE("accountRegister")
  },
  beforeDestroy() {
    this.$bus.$emit("verificationRegister",false)
  }
}
</script>

<style scoped>
  .nvd {
    margin-bottom: 50px;
  }
  .nvd .in-context {
    /*margin-top: 20px;*/
    text-align: right;
    margin: 20px 100px 0 0;
  }
  .nvd .in-context span {
    margin-bottom: 10px;
    color: slateblue;
    font-family: serif;
    font-size: larger;
  }
  .nvd .in-context .in-put, .error {
    vertical-align: middle;
  }
  .nvd .error {
    position: relative;
    top: -33px;
    left: -50px;
    float: right;
  }
  .nvd .in-context .in-put {
    width: 250px;
    height: 30px;
    border-radius:5px;
    border: 1px solid burlywood;
  }
</style>