      </ul>
    </div>
    <div class="reply" v-for="reply in topic.replyInformationList" :key="reply.id">
      <span style="color: skyblue;margin-right: 3px;">{{reply.author}}</span>
      <span style="font-family: cursive;margin-right: 3px;" v-show="reply.respondent !== ''">回复</span>
      <span style="color: lightpink;">{{reply.respondent}}</span>：{{reply.content}}
      <button v-if="reply.author !== $store.state.defaultContext.userName" class="btn btn-danger" @click="rep(reply.author)">回复</button>
    </div>
    <span class="reply-content" v-show="replyState && information !== ''">回复：<span>{{respondent}}</span></span>
    <input ref="text-input" @blur="inBlur" v-model="information" placeholder="评论" @keyup.enter="send(topic.id)">
  </div>
</template>

<script>
import axios from "axios";
import router from "@/router";
export default {
  name: "Dynamics",
  props: ['mySwitch','topic'],
  emits: ['refresh'],
  data() {
    return {
      fabulous: false, //赞
      collection: false, //收藏
      replyState: false,
      information:'',
      respondent: ''
    }
  },
  computed: {
    host() {
      return this.$store.state.host
    }
  },
  methods: {
    delTopic(topicId) {
        // operation: 'delDynamic',
      axios.delete(this.host + `delDynamic/${topicId}`)
      for (let i = 0; i < 2; i++) {
        this.$bus.$emit('refreshMyLogsPage')
      }
    },
    inBlur() {
      if (this.information === '') {
        this.$refs['text-input'].placeholder = '评论';
        this.replyState = false
      }
    },
    rep(author) {
      this.$nextTick(() => {
        this.$refs["text-input"].focus()
      })
      this.$refs["text-input"].placeholder = `回复：${author}`
      this.respondent = author
      this.replyState = true
    },
    send(topic) {
      const author = this.$store.state.defaultContext.userName
      if (author !== '' && author !== undefined && author !== null) {
        const content = this.information
        let data = {
          // operation: 'addReply',
          topic,
          author,
          content,
          respondent: ''
        }
        if (this.$refs["text-input"].placeholder !== '评论') {
          data.respondent = this.respondent
        }
        axios.post(this.host + 'addReply',  data)
        this.$bus.$emit('refreshMyLogsPage')
        this.information = ''
        this.$nextTick(() => {
          this.$refs["text-input"].blur()
        })
      } else {
        alert("你还没有登录,前往登录")
        router.push({path: '/LoginPage'})
      }
    }
  }
}
</script>

<style scoped>
  li {
    list-style: none;
  }
  .root {
    display: block;
    margin-left: 48px;
    margin-right: 48px;
    /*background-color: white;*/
  }
  .head {
    margin-bottom: 25px;
  }
  .head .headportrait {
    width: 50px;
    height: 50px;
    border-radius: 10px;
  }
  .headportrait,.name {
    vertical-align: middle;
  }
  .head .name {
    margin-left: 15px;
  }
  .del-btn {
    float: right;
  }
  .del-btn button {
    display: none;
  }
  .root:hover .del-btn button {
    display: block;
  }
  .title {

  }
  .title .content {
    margin-bottom: 10px;
    width: 620px;
    margin-top: 5px;
  }
  .picture img {
    width: 200px;
    height: 200px;
    margin-right: 8px;
  }
  .root .personality ul {
    display: flex;
    margin-top: 10px;
    margin-left: 450px;
  }
  .root .personality ul li {
    margin-left: 10px;
  }
  .reply {
    margin-top: 6px;
    padding: 5px;
  }
  .reply button {
    border: none;
    float: right;
    display: none;
    /*margin-top: 3px;*/
  }
  .reply:hover button{
    display: block;
  }
  .reply:before {
    content: initial;
  }

  .reply:last-child {
    border-bottom: none;
  }

  .reply:hover{
    background-color: #ddd;
  }
  .root input {
    width: 500px;
    height: 25px;
    padding: 0;
    margin: 10px 5px 5px 0;
    outline: none;
  }

  .root .reply-content {
    margin-right: 5px;
  }
  .root .reply-content span {
    color: #d22237;
  }

  .btn {
    display: inline-block;
    padding: 1px 12px;
    margin-bottom: 0;
    font-size: 14px;
    line-height: 20px;
    text-align: center;
    vertical-align: middle;
    cursor: pointer;
    box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.2), 0 1px 2px rgba(0, 0, 0, 0.05);
    border-radius: 4px;
  }
  .btn-danger {
    color: #fff;
    background-color: #da4f49;
    border: 1px solid #bd362f;
  }
  .btn-danger:hover {
    color: #fff;
    background-color: #bd362f;
  }
  .btn:focus {
    outline: none;
  }
</style>