       <input type="text" v-model="password" @blur="verificationPassword">
      <button @click="getLoginEmailCode">{{confirmTxt}}获取验证码</button>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "EmailLogin",
  data() {
    return {
      email: '@qq.com',
      account: '',
      password: '',
      confirmTxt: '',
      isConfirmTxt: false,
      timer: ''
    }
  },
  methods: {
    verificationAccount() {
      this.$bus.$emit('loginAccount', this.account + this.email)
    },
    verificationPassword() {
      this.$bus.$emit('loginPassword', this.password)
    },
    getLoginEmailCode() {
      // this.confirmTxt = 60
      // this.isConfirmTxt = true
      // this.timer = setInterval(() => {
      //   if (this.confirmTxt === 0) {
      //     this.confirmTxt = ''
      //     this.isConfirmTxt = false
      //     clearInterval(this.timer)
      //   } else {
      //     this.confirmTxt--
      //   }
      // },1000)
        // operation: 'getLoginEmailCode',
      const account = this.account + this.email
      axios.get(this.$store.state.host + `sign/${account}`).then(
          require => {
            if (!require.data.data.signStatus) alert("账号未注册")
            else {
              this.confirmTxt = 60
              this.isConfirmTxt = true
              this.timer = setInterval(() => {
                if (this.confirmTxt === 0) {
                  this.confirmTxt = ''
                  this.isConfirmTxt = false
                  clearInterval(this.timer)
                } else {
                  this.confirmTxt--
                }
              }, 1000)
            }
          }

      )
    }
  },
  mounted() {
    this.$bus.$emit('loginType', 'emailLogin')
  }
}
</script>

